<script>
export default {
  data() {
    return {
      currentStep: 1,
      selectedStyle: 1,
      isCustomStyle: false,
      customImage: '',
      opacity: 100,
      showOpacityModal: false,
      showPreviewModal: false,
      showLivePreviewModal: false,
      // Ë£ÅÂâ™Âå∫ÂüüÁõ∏ÂÖ≥
      cropArea: {
        left: 0,
        top: 0,
        width: 100,
        height: 100
      },
      // Â∫ïÂõæÁº©ÊîæÊØî‰æãÔºà100% ‰∏∫ÂéüÂßãÂ§ßÂ∞èÔºâ
      imageScale: 100,
      // ÊòØÂê¶‰ªéÈ¢ÑËßàÊâìÂºÄÁöÑË∞ÉÊï¥ÂºπÁ™ó
      fromPreview: false,
      isDragging: false,
      isResizing: false,
      dragStart: { x: 0, y: 0 },
      imageInfo: { width: 0, height: 0 },
      form: {
        title: '',
        deliveryDate: '',
        phone: '',
        wechat: '',
        content: ''
      }
    };
  },
  computed: {
    minDate() {
      const today = new Date();
      const year = today.getFullYear();
      const month = String(today.getMonth() + 1).padStart(2, '0');
      const day = String(today.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    },
    // Ëé∑Âèñ‰ø°‰ª∂Â∫ïÂõæË∑ØÂæÑ
    letterBackground() {
      if (this.isCustomStyle) {
        return this.customImage;
      }
      return `/static/xinxiang/xin${this.selectedStyle}.jpg`;
    }
  },
  methods: {
    // ÈÄâÊã©È¢ÑËÆæÊ†∑Âºè
    selectPresetStyle(index) {
      this.selectedStyle = index;
      this.isCustomStyle = false;
      this.customImage = '';
    },
    
    // ‰∏ä‰º†Ëá™ÂÆö‰πâÂõæÁâá
    uploadCustom() {
      uni.chooseImage({
        count: 1,
        sizeType: ['compressed'],
        sourceType: ['album', 'camera'],
        success: (res) => {
          this.customImage = res.tempFilePaths[0];
          this.isCustomStyle = true;
          this.opacity = 100;
          // ÈáçÁΩÆË£ÅÂâ™Âå∫Âüü
          this.cropArea = {
            left: 10,
            top: 10,
            width: 80,
            height: 80
          };
          // Ëé∑ÂèñÂõæÁâá‰ø°ÊÅØ
          uni.getImageInfo({
            src: res.tempFilePaths[0],
            success: (info) => {
              this.imageInfo = {
                width: info.width,
                height: info.height
              };
            }
          });
          this.showOpacityModal = true;
          uni.showToast({ title: 'Ëá™ÂÆö‰πâÂ∫ïÂõæÂ∑≤ÈÄâÊã©', icon: 'success' });
        }
      });
    },
    
    // Ë∞ÉÊï¥ÈÄèÊòéÂ∫¶ÔºàÊªëÂä®‰∏≠Ôºâ
    onOpacityChanging(e) {
      this.opacity = e.detail.value;
    },
    
    // Ë∞ÉÊï¥ÈÄèÊòéÂ∫¶ÔºàÊùæÂºÄÔºâ
    onOpacityChange(e) {
      this.opacity = e.detail.value;
    },
    
    // Ë∞ÉÊï¥ÂõæÁâáÁº©ÊîæÔºàÊªëÂä®‰∏≠Ôºâ
    onScaleChanging(e) {
      this.imageScale = e.detail.value;
    },
    
    // Ë∞ÉÊï¥ÂõæÁâáÁº©ÊîæÔºàÊùæÂºÄÔºâ
    onScaleChange(e) {
      this.imageScale = e.detail.value;
    },
    
    // ÂºÄÂßãÊãñÂä®Ë£ÅÂâ™Ê°Ü
    startDrag(e) {
      this.isDragging = true;
      this.dragStart = {
        x: e.touches[0].clientX,
        y: e.touches[0].clientY
      };
    },
    
    // ÊãñÂä®Ë£ÅÂâ™Ê°Ü
    onDrag(e) {
      if (!this.isDragging) return;
      
      const deltaX = e.touches[0].clientX - this.dragStart.x;
      const deltaY = e.touches[0].clientY - this.dragStart.y;
      
      // ËΩ¨Êç¢‰∏∫ÁôæÂàÜÊØîÔºàÂÅáËÆæÈ¢ÑËßàÂå∫ÂüüÂÆΩÂ∫¶‰∏∫ 600rpxÔºâ
      const percentX = (deltaX / 600) * 100;
      const percentY = (deltaY / 600) * 100;
      
      let newLeft = this.cropArea.left + percentX;
      let newTop = this.cropArea.top + percentY;
      
      // ËæπÁïåÈôêÂà∂
      newLeft = Math.max(0, Math.min(100 - this.cropArea.width, newLeft));
      newTop = Math.max(0, Math.min(100 - this.cropArea.height, newTop));
      
      this.cropArea.left = newLeft;
      this.cropArea.top = newTop;
      
      this.dragStart = {
        x: e.touches[0].clientX,
        y: e.touches[0].clientY
      };
    },
    
    // ÁªìÊùüÊãñÂä®
    endDrag() {
      this.isDragging = false;
      this.isResizing = false;
    },
    
    // ÂºÄÂßãË∞ÉÊï¥Â§ßÂ∞è
    startResize(e) {
      this.isResizing = true;
      this.dragStart = {
        x: e.touches[0].clientX,
        y: e.touches[0].clientY
      };
      e.stopPropagation();
    },
    
    // Ë∞ÉÊï¥Â§ßÂ∞è
    onResize(e) {
      if (!this.isResizing) return;
      
      const deltaX = e.touches[0].clientX - this.dragStart.x;
      const deltaY = e.touches[0].clientY - this.dragStart.y;
      
      const percentX = (deltaX / 600) * 100;
      const percentY = (deltaY / 600) * 100;
      
      let newWidth = this.cropArea.width + percentX;
      let newHeight = this.cropArea.height + percentY;
      
      // ËæπÁïåÈôêÂà∂
      newWidth = Math.max(20, Math.min(100 - this.cropArea.left, newWidth));
      newHeight = Math.max(20, Math.min(100 - this.cropArea.top, newHeight));
      
      this.cropArea.width = newWidth;
      this.cropArea.height = newHeight;
      
      this.dragStart = {
        x: e.touches[0].clientX,
        y: e.touches[0].clientY
      };
      
      e.stopPropagation();
    },
    
    // ÊâìÂºÄÈ¢ÑËßàÂºπÁ™ó
    openPreview() {
      this.showLivePreviewModal = true;
    },
    
    // ÂÆåÊàêË∞ÉÊï¥ÔºàÊô∫ËÉΩÂà§Êñ≠ÊòØÂê¶ÈúÄË¶ÅËøîÂõûÈ¢ÑËßàÔºâ
    finishAdjust() {
      this.showOpacityModal = false;
      // Â¶ÇÊûúÊòØ‰ªéÈ¢ÑËßàÊâìÂºÄÁöÑÔºåËøîÂõûÈ¢ÑËßàÔºõÂê¶ÂàôÂÖ≥Èó≠modal
      if (this.fromPreview) {
        this.showLivePreviewModal = true;
        this.fromPreview = false;
      }
    },
    
    // ‰ªéÈ¢ÑËßàÊâìÂºÄË∞ÉÊï¥ÂºπÁ™ó
    openAdjustFromPreview() {
      this.fromPreview = true;
      this.showLivePreviewModal = false;
      this.showOpacityModal = true;
    },
    
    // Êó•ÊúüÈÄâÊã©
    onDateChange(e) {
      this.form.deliveryDate = e.detail.value;
    },
    
    // ‰∏ã‰∏ÄÊ≠•
    nextStep() {
      if (!this.selectedStyle && !this.isCustomStyle) {
        uni.showToast({ title: 'ËØ∑ÈÄâÊã©‰ø°‰ª∂Ê†∑Âºè', icon: 'none' });
        return;
      }
      this.currentStep = 2;
    },
    
    // ‰∏ä‰∏ÄÊ≠•
    prevStep() {
      this.currentStep = 1;
    },
    
    // Êèê‰∫§‰ø°‰ª∂
    submitLetter() {
      // È™åËØÅÂøÖÂ°´È°π
      if (!this.form.title) {
        uni.showToast({ title: 'ËØ∑Â°´ÂÜô‰ø°‰ª∂‰∏ªÈ¢ò', icon: 'none' });
        return;
      }
      if (!this.form.deliveryDate) {
        uni.showToast({ title: 'ËØ∑ÈÄâÊã©ÈÄÅËææÊó∂Èó¥', icon: 'none' });
        return;
      }
      if (!this.form.phone) {
        uni.showToast({ title: 'ËØ∑Â°´ÂÜôÊâãÊú∫Âè∑', icon: 'none' });
        return;
      }
      if (this.form.phone.length !== 11) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÊâãÊú∫Âè∑', icon: 'none' });
        return;
      }
      if (!this.form.content) {
        uni.showToast({ title: 'ËØ∑Â°´ÂÜô‰ø°‰ª∂ÂÜÖÂÆπ', icon: 'none' });
        return;
      }

      // ÊûÑÂª∫‰ø°‰ª∂Êï∞ÊçÆ
      const letterData = {
        style: this.isCustomStyle ? 'custom' : this.selectedStyle,
        customImage: this.customImage,
        opacity: this.opacity,
        cropArea: this.cropArea,
        title: this.form.title,
        deliveryDate: this.form.deliveryDate,
        phone: this.form.phone,
        wechat: this.form.wechat,
        content: this.form.content,
        createTime: new Date().toLocaleString()
      };

      // ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
      try {
        const letters = uni.getStorageSync('xinxiang_letters') || [];
        letters.unshift(letterData);
        uni.setStorageSync('xinxiang_letters', letters);
        
        // ÊòæÁ§∫È¢ÑËßàÂºπÁ™ó
        this.showPreviewModal = true;
      } catch (e) {
        uni.showToast({ title: 'Êèê‰∫§Â§±Ë¥•ÔºåËØ∑ÈáçËØï', icon: 'none' });
      }
    },
    
    // ÂÖ≥Èó≠È¢ÑËßàÂºπÁ™óÂπ∂ËøîÂõû
    closePreviewAndBack() {
      this.showPreviewModal = false;
      setTimeout(() => {
        uni.navigateBack();
      }, 300);
    }
  }
};
</script>

